import React from "react";
import '../styles/FoodSearch.css'
//  
function FoodSearch({keywordSearch}) {
    return (
        <div className="foodSearch-container">
            <div className="foodSearch-body">
                <input type="text" className="keyword" placeholder=" "></input>
                <button className="searchBtn" onClick={keywordSearch}></button>
            </div>
        </div>
    )
}
export default FoodSearch