import React from "react";

function Dday() {
    const remainingSalaryDay = Math.ceil((new Date('2023-09-18').getTime() - Date.now())/1000/60/60/24);
    // console.log(remainingSalaryDay)
    return (
        <>
            {remainingSalaryDay > 0 ? <p>  {remainingSalaryDay}  </p>
            : <p>   !!@!#~</p>}
            
        </>
    )
}

export default Dday