import React, { useEffect } from "react";
import '../styles/Sidebar.css'
import { Link } from "react-router-dom"
import { useCookies } from 'react-cookie';


function Sidebar({ homeIcons, buttonFlag, flagChange, setLoginModalState, setOptionModalState }) {
    const [cookies, setCookie, removeCookie] = useCookies(['accessToken']);
    const anotheClickHideSidebar = (e) => { //       

        const sidebarmenu = document.querySelector('.window-bar') //  
        const footerButton = document.querySelector('footer button')   // 
        const footerButtonChildDiv = footerButton.childNodes[0]  // div :   div
        const footerButtonChildDivImg = footerButton.childNodes[0].children[0] // img :  
        const footerButtonChildDivSpan = footerButton.childNodes[0].children[1] // span :  text

        if (sidebarmenu.classList.contains('showSidebar') //    
            && e.target !== footerButton // footer>button     
            && e.target !== footerButtonChildDiv // button>div     
            && e.target !== footerButtonChildDivImg // ' '    
            && e.target !== footerButtonChildDivSpan  // ''        
        ) {
            flagChange() //   footer buttonFlag false   props  .
        }

    }

    useEffect(() => { //        .
        document.addEventListener('click', anotheClickHideSidebar);
        return () => {
            document.removeEventListener('click', anotheClickHideSidebar);
        };
    }, []);
    const handleLoginModal = () => {
        setLoginModalState(true)
    }
    const handleOptionModal = () => {
        setOptionModalState(true)
    }
    const handleLogout = () => { 
        removeCookie('accessToken');
        window.location.reload()
    }

    return (
        <>
            <div className={`window-bar ${buttonFlag ? 'showSidebar' : ''}`}>  {/* buttonFlag true sidebar  */}
                <div className="window-textbar"><h5>Window<span>95</span></h5></div>
                <div className="window-menubar">
                    {homeIcons.map((icon, id) => {
                        return (
                            <React.Fragment key={id}> 
                                {icon.iconTitle === '' ? (
                                    <div onClick={handleLoginModal}>
                                        <img src={icon.iconSrc} alt="" />
                                        {icon.iconTitle}
                                    </div>
                                ) : icon.iconTitle === '' ? (
                                    <div onClick={handleOptionModal}>
                                        <img src={icon.iconSrc} alt="" />
                                        {icon.iconTitle}
                                    </div>
                                ) : icon.iconTitle === '' ? (
                                    <div onClick={handleLogout}>
                                        <img src={icon.iconSrc} alt="" />
                                        {icon.iconTitle}
                                    </div>)
                                    : (
                                        <Link to={icon.url} state={icon.iconTitle}>
                                            <div>
                                                <img src={icon.iconSrc} alt="" />
                                                {icon.iconTitle}
                                            </div>
                                        </Link>
                                    )}
                            </React.Fragment>
                        )
                    })}
                </div>
            </div>
        </>
    )
}

export default Sidebar