import React, {useState, useEffect} from "react";
import { useNavigate } from "react-router-dom";
import '../styles/Icons.css'

import { useCookies } from 'react-cookie';

function Icon({ src, children, href, setUserInfo, userInfo, setOptionModalState, setLoginModalState }) {
    const [cookies, setCookie, removeCookie] = useCookies(['accessToken']);
    
    const [iconActiveFlag, setIconActiveFlag] = useState(''); // Icon       state
    const changeIconFlag = (e) => { // Icon  css active   state .
        setIconActiveFlag('clicked');
        removeAndAddActiveClass(e);
    };
  
    const removeAndAddActiveClass = (e) => { // Icon   active  icon active ,  icon active .
        const icons = document.querySelectorAll('.main-icons');
        const h5 = document.querySelectorAll('h5');
        icons.forEach((icon) => {
            icon.classList.remove('blurred');
        });
        h5.forEach((h5) => {
            h5.classList.remove('active');  
        })
        e.currentTarget.classList.add('blurred');
        e.currentTarget.lastChild.classList.add('active');
    }
    const handleClickOutside = (e) => { // Icon   active .
      if (
        e.target.className !== 'icon-image' &&
        e.target.className !== 'main-icons' &&
        e.target.tagName !== 'IMG' &&
        e.target.tagName !== 'H5'
      ) {
        setIconActiveFlag('');
      }
    };
    
    const navigate = useNavigate() // useNavigate hook    .
    const moveToPage = () => { // Icon     .
      if(children === '') { //         
        setOptionModalState(true) 
      } else if(children === ''){
        setLoginModalState(true);
      } else if(children === ''){
          removeCookie('accessToken');
          window.location.reload()
        console.log('out')
      }
      else {
        navigate(href, {state:children})
      }
    }

    useEffect(() => { // Icon      .
      document.addEventListener('click', handleClickOutside);
      return () => {
        document.removeEventListener('click', handleClickOutside);  // Icon      .
      };
    }, []);
    return (
      <>
        <div
          className={`main-icons ${iconActiveFlag ==='clicked' ? 'blurred' : ''}`}
          onClick={changeIconFlag} onDoubleClick={moveToPage} // onClick  , onDoubleClick   
        >
          <div className="icon-image">
            <img src={src} alt="" />
          </div>
          <h5 className={`${iconActiveFlag==='clicked' ? 'active' : ''}`}>{children}</h5>
        </div>
      </>
    );
  }

export default Icon