import React, { useState, useEffect } from "react";
import {
    Header, 
    Footer,
    LoungeInputEdit,
    LoungeModal, 
    LoungeRegisterInput, 
    LoungePagenation, 
    SnsTimeFormat, 
    LoungeCommentRegister, 
} from "../components"
import "../styles/Lounge.css"

function Lounge({ userInfo }) {

    const [chat, setChat] = useState([]) // db   
    const [comment, setComment] = useState([]) // db    
    const [modalPosition, setModalPosition] = useState(null) //   
    const [clickData, setClickData] = useState(null) // , ,       
    const [passwordText, setPasswordText] = useState(null) //  
    const [passwordMatched, setPasswordMatched] = useState(false) //   
    const [updateInputValue, setUpdateInputValue] = useState('') //     
    const [dbCode, setDbCode] = useState('') // db     
    const [commentCode, setCommentCode] = useState('') // db      

    const [modalStyle, setModalStyle] = useState(false) //      
    const [toggleComment, setToggleComment] = useState(false) //  
    const [depth, setDepth] = useState('') //   

    const [page, setPage] = useState(1) //     
    const limit = 8 //          
    const totalPosts = chat.length //      
    const offset = (page - 1) * limit //     
    const currentPosts = chat.slice(0).reverse().slice(offset, offset + limit) //     1   .

    // DB 
    const getChatData = async () => {
        await fetch('http://127.0.0.1:5300/lounge', {
            method: 'get',
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(res => res.json())
            .then(data => setChat(data))
    }
    const getCommentData = async () => {
        await fetch('http://127.0.0.1:5300/loungeComment', {
            method: 'get',
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(res => res.json())
            .then(data => setComment(data))
    }

    useEffect(() => {
        getChatData()
        getCommentData()
    }, [])
    // console.log(chat)

    useEffect(() => {
        const clickModalOutside = (e) => { //     
            e.stopPropagation()
            if (modalPosition &&
                e.target.className !== "edit" && e.target.className !== "delete"
                && e.target.className !== "comment" && e.target.className !== "confirm"
                && e.target.className !== "text_function"
                && e.target.className !== "passwordCheck"
                && e.target.className !== 'editPassword') {
                setModalPosition(null)
                setUpdateInputValue('')
            }
        }
        document.addEventListener('click', clickModalOutside)
        return () => {  // clean up
            document.removeEventListener('click', clickModalOutside)
        }
    }, [modalPosition]) // modalPosition  useEffect 

    //  
    const registerText = () => { //        
        const id = document.querySelector("#nickname").value;
        const password = document.querySelector("#password").value;
        const text = document.querySelector("#text").value;

        fetch('http://127.0.0.1:5300/lounge', { // db 
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                nickname: id,
                password: password,
                text: text
            })
        })
        getChatData() // db  

        //  
        document.querySelector("#nickname").value = "";
        document.querySelector("#password").value = "";
        document.querySelector("#text").value = "";
    }


    //  
    const HandleModalEdit = async (e) => {
        e.stopPropagation() //  ,    ,     ()
        const mongoDbId = e.target.parentNode.parentNode.parentNode.firstChild.children[2].innerText
        const depth = e.target.parentNode.parentNode.parentNode.firstChild.children[3].innerText

        // console.log(e.target.closest('.lounge__textOutput__text').querySelector('.paragraph-id').innerText) // comment   
        setModalStyle(false)
        setClickData(e.target)
        setDepth(depth)
        if (depth === '0') {
            if (dbCode !== mongoDbId) {
                setToggleComment(false)
            }
            setDbCode(mongoDbId) //  db 
        }
        if (depth === '1') {
            setCommentCode(mongoDbId) //  db 
        }

        const rect = e.target.getBoundingClientRect();

        setModalPosition({
            top: rect.top + rect.height,
            left: rect.left,
        });
    }

    // ,  ( )
    const editModalText = async (e) => {
        e.stopPropagation()
        const id = clickData.parentNode.parentNode.previousSibling.firstChild.innerText
        const editPassword = document.querySelector("#editPassword").value
        const text = clickData.parentNode.parentNode.firstChild.innerText
        console.log(editPassword)
        if (clickData.innerText === "" && depth === '0') {
            try {
                const response = await fetch('http://127.0.0.1:5300/lounge/edit', {
                    method: 'post',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        _id: dbCode,
                        password: editPassword,
                    })
                })
                if (response.ok === true && editPassword !== "") {
                    setPasswordMatched(true);
                    setModalPosition(null)
                    setPasswordText(editPassword)
                    getChatData()
                } else {
                    setPasswordMatched(false);
                    setModalStyle(true)
                    setModalPosition(modalPosition)
                }
            }

            catch (error) {
                console.log(error);
            }
        } else if (clickData.innerText === "" && depth === '1') {
            try {
                const response = await fetch('http://127.0.0.1:5300/loungeComment/edit', {
                    method: 'post',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        _id: commentCode,
                        password: editPassword,
                    })
                })
                if (response.ok === true && editPassword !== "") {
                    setPasswordMatched(true);
                    setModalPosition(null)
                    setPasswordText(editPassword)
                    getCommentData()
                } else {
                    setPasswordMatched(false);
                    setModalStyle(true)
                    setModalPosition(modalPosition)
                }
            }

            catch (error) {
                console.log(error);
            }
        }
        if (clickData.innerText === "" && depth === '0') {
            try {
                fetch('http://127.0.0.1:5300/lounge/delete', {
                    method: 'delete',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        _id: dbCode,
                        password: editPassword,
                    })
                })
                    .then(res => {
                        if (res.ok === true) {
                            getChatData()
                            setModalPosition(null)
                            setModalStyle(false)
                        } else {
                            setModalStyle(true)
                            setModalPosition(modalPosition)
                            console.log('')
                        }
                    }
                    )
            } catch (error) {
                console.log(error);
            }
        } else if (clickData.innerText === "" && depth === '1') {
            try {
                fetch('http://127.0.0.1:5300/loungeComment/delete', {
                    method: 'delete',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify({
                        _id: commentCode,
                        password: editPassword,
                    })
                })
                    .then(res => {
                        if (res.ok === true) {
                            getCommentData()
                            setModalPosition(null)
                            setModalStyle(false)
                        } else {
                            setModalStyle(true)
                            setModalPosition(modalPosition)
                            console.log('')
                        }
                    }
                    )
            } catch (error) {
                console.log(error);
            }
        }
    }

    //      
    const confirmEditText = (e, index) => {
        e.stopPropagation()
        const editedText = document.querySelector(".editText").value
        console.log(depth)
        setUpdateInputValue(editedText)

        if (e.target.innerText === "" && depth === '0') {
            fetch('http://127.0.0.1:5300/lounge/edit', {
                method: 'put',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    _id: dbCode,
                    password: passwordText,
                    text: editedText,
                    lastModifiedAt: new Date()
                })
            })
                .then(res => res.json()) //    json 
                .then(data => { // json   data   
                    const updatedChat = chat.map(c => {
                        if (chat._id === dbCode) {
                            return { ...chat, text: editedText } //       
                        } else {
                            return { ...chat }
                        }
                    })
                    setChat(updatedChat) //       
                    setUpdateInputValue('') //      input 
                    setPasswordMatched(false)
                    getChatData()
                })
        } else if (e.target.innerText === "" && depth === '1') {
            fetch('http://127.0.0.1:5300/loungeComment/edit', {
                method: 'put',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    _id: commentCode,
                    password: passwordText,
                    text: editedText,
                    lastModifiedAt: new Date()
                })
            })
                .then(res => res.json()) //    json 
                .then(data => { // json   data   
                    const updatedChat = chat.map(c => {
                        if (chat._id === commentCode) {
                            return { ...chat, text: editedText } //       
                        } else {
                            return { ...chat }
                        }
                    })
                    setComment(updatedChat) //       
                    setUpdateInputValue('') //      input 
                    setPasswordMatched(false)
                    getCommentData()
                })
        }

        if (e.target.innerText === "") {
            setPasswordMatched(false)
            setUpdateInputValue('') //      input 
        }
    }
    const onChange = (e) => { // input    
        setUpdateInputValue(e.target.value)
    }
    const handleComment = (e) => {
        e.stopPropagation()
        const mongoDbId = e.target.parentNode.parentNode.parentNode.firstChild.children[2].innerText
        const depth = e.target.parentNode.parentNode.parentNode.firstChild.children[3].innerText
        setDbCode(mongoDbId)
        if (e.target.innerText === "") {
            setClickData(e.target)
            setToggleComment(!toggleComment)
            setDepth(depth)
            if (toggleComment) {
                setModalPosition(null)
            }
        }
    }

    return (
        <>
            <Header></Header>
            <div className="loung__body" >
                <div className="lounge__textOutput">
                    <div className="lounge__textOutput__title">
                        <h3>Small Talk Lounge</h3>
                        <hr />
                    </div>

                    {chat.length !== 0 && currentPosts.map((chat, index) => {
                        return (
                            <React.Fragment key={chat._id}>
                                <div>
                                    <div className="lounge__textOutput__text" >
                                        <div className="nickname">
                                            <span><img src='images/loungeuser.png' alt='userProfile' />{chat.nickname} </span> <SnsTimeFormat chatTime={chat.date} /> <span className='paragraph-id'>{chat._id}</span><span className="depth">0</span>
                                        </div>
                                        <div className="text__function" >
                                            <LoungeInputEdit //     input  
                                                passwordMatched={passwordMatched}
                                                HandleModalEdit={(e, index) => HandleModalEdit(e, index)}
                                                confirmEditText={(e) => confirmEditText(e, index)}
                                                modalPosition={modalPosition}
                                                clickData={clickData}
                                                onChange={onChange}
                                                updateInputValue={updateInputValue}
                                                chat={chat}
                                                dbCode={dbCode}
                                                handleComment={handleComment}
                                                depth={depth}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <LoungeCommentRegister  //  
                                    passwordMatched={passwordMatched}
                                    modalPosition={modalPosition}
                                    comment={comment}
                                    toggleComment={toggleComment}
                                    dbCode={dbCode}
                                    commentCode={commentCode}
                                    chat={chat}
                                    getCommentData={getCommentData}
                                    HandleModalEdit={HandleModalEdit}
                                    updateInputValue={updateInputValue}
                                    editModalText={editModalText}
                                    onChange={onChange}
                                    modalStyle={modalStyle}
                                    confirmEditText={confirmEditText}
                                    clickData={clickData}
                                    depth={depth}
                                />
                            </ React.Fragment>
                        )
                    })}
                    <LoungeModal  // ,    
                        modalPosition={modalPosition}
                        updateInputValue={updateInputValue}
                        editModalText={editModalText}
                        onChange={onChange}
                        modalStyle={modalStyle}
                    />

                </div>
                <LoungeRegisterInput
                    registerText={registerText}
                />
                <LoungePagenation
                    page={page}
                    setPage={setPage}
                    limit={limit}
                    totalPosts={totalPosts}
                />
            </div>
            <Footer userInfo={userInfo}></Footer>
        </>
    )
}

export default Lounge

