import '../styles/Header.css'
import homeIcons from "../HomeIconsData";
import { useNavigate, useLocation } from "react-router-dom"


function Header({}){

    const navigate = useNavigate()
    const moveToHomePage = () => { //  X   
        navigate('/')
    }
    const iconExternalState = useLocation()  // Icon  useNavigate state (icon.title) Sidebar  state(icon.title) iconText  
    const icon = homeIcons.filter(i => iconExternalState.pathname === i.url) // iconText.pathname i.url   icon 

    return(
        <div className="header">
            <h5>{icon.length !== 0 && icon[0].iconTitle}</h5>  
            <div className='button-container'>
                <button onClick={moveToHomePage}>X</button>
            </div>
        </div>    
    )
}

export default Header