import React from "react";

function LoungeInputEdit({chat, index, passwordMatched, modalPosition, HandleModalEdit, confirmEditText,  dbCode, handleComment,depth}){
    return (
        <>
            {/* -> ->    */}
            {passwordMatched && !modalPosition && dbCode === chat._id && depth === '0' ? //  ,  , db    , depth=0() 
            <>  
                <div className="editInputDiv" >
                    <input type="text" className="editText" defaultValue={chat.text} />
                </div>

                <div className="text__function__edit">
                    <span className="confirm" onClick={(e)=>confirmEditText(e, index)}></span>
                </div>
                    <div className="text__function__cancle" ><span className="delete" onClick={(e)=>confirmEditText(e, index)}></span></div>
            </>
        :  
        <>
            {/*     */}
            <p>{chat.text}</p>
            <div className="text__function__edit">
                <span className="edit" onClick={(e, index)=>HandleModalEdit(e, index)} ></span>
            </div>
            <div className="text__function__delete"><span className="delete" onClick={(e, index)=>HandleModalEdit(e ,index)} ></span></div>
            <div className="text__function__comment"><span className="comment" onClick={(e, index)=>handleComment(e ,index)}></span></div>
        </>
        }

    </>
    )
}

export default  LoungeInputEdit