import React, {useState, useEffect} from "react"
import LoungeButton from "./LoungeButton"
import LoungePageWrap from "./LoungePageWrap"


function LoungePagenation  ({page, setPage, totalPosts, limit}) {
    const pageLimit = 5 //   
    const numPages = Math.ceil(totalPosts / limit) //  

    const [totalPageArray, setTotalPageArray] = useState([]) //   
    const [currentPagesArray, setCurrentPagesArray] = useState([]) //   , 5   
    const [btnActive, setBtnActive] = useState(page) //     
    
    useEffect(()=> {
        const slicedPageArray = slicedPageArrayByLimit(numPages ,pageLimit) //   
        setTotalPageArray(slicedPageArray) 
        setCurrentPagesArray(slicedPageArray[0]) 
        // console.log(numPages, slicedPageArray, currentPagesArray) // 
    }, [numPages])

    useEffect(()=> {  //       currentPagesArray 
        // console.log(totalPageArray, currentPagesArray) // 
        if(page % pageLimit === 1){  //      5  1        
            setCurrentPagesArray(totalPageArray[Math.floor(page / pageLimit)])  
        }else if(page % pageLimit === 0){ // pageLimit  totalpageArray  index , index 0  -1
            setCurrentPagesArray(totalPageArray[Math.floor(page / pageLimit) - 1]) 
        }
    }, [page, totalPageArray])

    const handlePageChange = (e ,currentPage) => { //   
        setPage(currentPage)
        setBtnActive(currentPage) //  
        e.stopPropagation()
    }

    const slicedPageArrayByLimit = (numPages, pageLimit) => { //   
        const totalPageArray = Array(numPages)
        .fill()
        .map((_, i) => i + 1)
        return Array(Math.ceil(numPages / pageLimit))
        .fill()
        .map(()=> totalPageArray.splice(0, pageLimit))
    }


    return(
        <>
        {currentPagesArray === undefined ? null : //    
            (<LoungePageWrap>

                <LoungeButton 
                onClick={()=> {
                    setPage(page - 1) 
                    setBtnActive(page-1)
                }} 
                disabled={page === 1}>
                    <span className="material-symbols-outlined">arrow_back_ios</span>
                </LoungeButton> 
                
                {currentPagesArray?.map(( currentPage , i)=> { //  
                    return(
                        <LoungeButton
                            key={i+1}
                            className={ currentPage === btnActive ? 'active' : ''} 
                            onClick={(e)=>handlePageChange(e, currentPage)} //  
                            aria-current={page === i + 1 ? 'page' : undefined} //  
                            > { currentPage }
                        </LoungeButton> 
                    )
                })
                
                }
                <LoungeButton 
                    onClick={()=>{ 
                        setPage(page + 1) 
                        setBtnActive(page+1)       
                    }} 
                    disabled={page === numPages}>
                        <span className="material-symbols-outlined">arrow_forward_ios</span>
                </LoungeButton>

            </LoungePageWrap>
            )}
            <div className="pagenation__summary">
              <span className="pagenation__text">Page {page} of {numPages}</span>
              <span className="pagenation__text">Total {totalPosts} Posts</span>
            </div>   
        </>
    )

}

export default LoungePagenation

