import React, { useState, useEffect } from "react";

function Calendar() {
    const [selectDate, setSelectDate] = useState({ 
        year : new Date().getFullYear(), 
        month : new Date().getMonth()
    })

    const makeCalendar = (year, month) => {
        const firstDay = new Date(year,month,1).getDay(); // 1  ( 0 ~  6 .)
        // console.log(firstDay)
        const dateArray = []; //      
        let nextMonth = 0; //           (Date getDate       1  31   Date() 32  getDate  1  .)
        let day = 1; //     1 1  .
        for(let i = 0; i < firstDay; i++){ //      1  -1  .      .
            dateArray.push(-1)
        }
        while(nextMonth < 2){ // nextMonth getDate() 1   1        getDate()   1   1 2   . 
            
            if(new Date(year,month,day).getDate() === 1) nextMonth++; // getDate()   1  nextMonth   (1      2 )
            // console.log(day)
            // console.log(new Date(2023,8,day).getDate())
            // console.log(nextMonth)
            if(nextMonth < 2) dateArray.push(day); //    day .
            day++; // day 1      .
        }
        // console.log(dateArray)
        const weekCount = dateArray.length%7 !== 0 ? Math.floor(dateArray.length / 7) + 1 : dateArray.length / 7; //         .
        const weekCountArray = new Array(weekCount).fill(1).map((a, i) => i); // map       .

        return (
            <table>
                <thead>
                    <tr>
                        <th>S</th>
                        <th>M</th>
                        <th>T</th>
                        <th>W</th>
                        <th>T</th>
                        <th>F</th>
                        <th>S</th>
                    </tr>
                </thead>
                <tbody>
                    {weekCountArray.map((week, id) => {
                        const innerDate = [];
                        for(let i = 0; i < 7; i++){ // 1     .
                            innerDate.push(dateArray[i+(7*id)])
                        }
                        return (
                            <tr key={id}>{innerDate.map((d, index) => {
                                return (
                                    <td key={index}>{d === -1? '' : d}</td> // 1    td      .
                                )
                            })}</tr>
                        )
                    })}
                </tbody>
            </table>
        )
    }

    useEffect(() => {
        //     
        if(selectDate.year === new Date().getFullYear() && selectDate.month === new Date().getMonth()){
            const td = document.querySelectorAll('.Calendar-container tbody tr td');

            td.forEach(td => {
                if(td.innerText == new Date().getDate()){
                    td.style.backgroundColor = 'blue';
                    td.style.color = '#fff';
                    td.style.fontWeight = 'bold';
                }
            })
        }else{
            //      
            const td = document.querySelectorAll('.Calendar-container tbody tr td');

            td.forEach(td => {
                    td.style.backgroundColor = '';
                    td.style.color = '';
                    td.style.fontWeight = '';
            })
        }
    },[selectDate])

    const decreaseMonth = () => {
        const year = selectDate.month <= 0 ? selectDate.year - 1 : selectDate.year;
        const month = selectDate.month <= 0 ? 11 : selectDate.month - 1;
        setSelectDate({ year, month })
    }

    const increaseMonth = () => {
        const year = selectDate.month >= 11 ? selectDate.year + 1 : selectDate.year;
        const month = selectDate.month >= 11 ? 0 : selectDate.month + 1;
        setSelectDate({ year, month })
    }

    return (
        <div className="Calendar-container">
            <div className="Calendar-select-container">
                <button onClick={decreaseMonth}className='Calendar-btn'></button>
                <span>{`${selectDate.year} ${selectDate.month+1}`}</span>
                <button onClick={increaseMonth} className='Calendar-btn'></button>
            </div>
            {makeCalendar(selectDate.year, selectDate.month)}
        </div>
    )
}

export default Calendar